<template>
  <div id="app">

    <Layout>
      <Header class="layout-header" :style="{color:'#fff',fontSize:'18px'}">
        <div class="header-logo">
          <img src="./assets/weima-logo.png"/>
        </div>
        威马金融计算器
      </Header>
      <Layout class="layout-content">
        <Content :style="{background: '#fff', marginLeft: '10px'}">
          <Row>
            <Col span="24" class="input-info">
              <label>续航里程：</label>
              <Select v-model="kilo" style="width:200px">
                  <Option v-for="item in kiloList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </Col>
            <Col span="24" class="input-info">
              <label>总价：</label>
               <Input v-model="carPrice" placeholder="车价" clearable style="width: 200px" number><span slot="prepend">车价</span></Input>
              <Input v-model="otherPrice" placeholder="其他" clearable style="width: 200px" number><span slot="prepend">其他</span></Input> = {{total}}
            </Col>
            <Col span="24" class="input-info">
              <label>客户年化利率：</label>
               <Input v-model="percentRate" placeholder="Enter something..." clearable style="width: 200px"></Input>%
            </Col>
            <Col span="24" class="input-info">
              <label>金融方案：</label>
              <Select v-model="plan" style="width:200px">
                  <Option v-for="item in planList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </Col>
          </Row>

          <Row :gutter="16" style="margin:0">

            <Col class="plan" v-show="tab1Show">
              <div class="plan-header">方案一：乐享“无”忧</div>
              <div class="plan-content">
                <ul class="plan-info">
                  <li><label class="plan-label">贷款期限(月)</label>
                    <RadioGroup v-model="plan2Period" type="button">
                        <Radio :label="24"></Radio>
                        <Radio :label="36"></Radio>
                        <Radio :label="48"></Radio>
                        <Radio :label="60"></Radio>
                    </RadioGroup>
                  </li>
                  <li><label class="plan-label">首付比例</label><Slider v-model="plan2FirstRate" :min="15" show-input></Slider> %</li>
                  <li><label class="plan-label">首付金额</label>{{plan2.firstPayment}}</li>
                  <li><label class="plan-label">融资比例</label>{{plan2.financeRate}} %</li>
                  <li><label class="plan-label">融资金额</label>{{plan2.financePayment}}</li>
                  <li><label class="plan-label">尾款比例</label>{{plan2.finalRate}} %</li>
                  <li><label class="plan-label">尾款金额</label>{{plan2.finalPayment}}</li>
                </ul>
                <ul class="plan-result">
                  <li><label>月供</label>{{plan2.perMonth}}</li>
                  <li><label>利息(天)</label>{{plan2.interest}}</li>
                </ul>
              </div>
            </Col>
            <Col class="plan" v-show="tab2Show">
               <div class="plan-header">方案二：轻松入“威”-定额贷</div>
               <div class="plan-content">
                 <ul class="plan-info">
                   <li><label class="plan-label">贷款期限(月)</label>
                     <RadioGroup v-model="plan3Period" type="button">
                         <Radio :label="18"></Radio>
                         <Radio :label="24"></Radio>
                         <Radio :label="36"></Radio>
                         <Radio :label="48"></Radio>
                     </RadioGroup>
                   </li>
                   <li><label class="plan-label">首付比例</label>{{plan3.firstRate}} %</li>
                   <li><label class="plan-label">首付金额</label>{{plan3.firstPayment}}</li>
                   <li><label class="plan-label">融资比例</label>{{plan3.financeRate}} %</li>
                   <li><label class="plan-label">融资金额</label>{{plan3.financePayment}}</li>
                   <li><label class="plan-label">尾款比例</label>{{plan3.finalRate}} %</li>
                   <li><label class="plan-label">尾款金额</label>{{plan3.finalPayment}}</li>
                 </ul>
                 <ul class="plan-result">
                   <li><label>月供</label>{{plan3.perMonth}}</li>
                   <li><label>利息(天)</label>{{plan3.interest}}</li>
                 </ul>
               </div>
            </Col>
            <Col  class="plan" v-show="tab3Show">
              <div class="plan-header">方案三：5050</div>
              <div class="plan-content">
                <ul class="plan-info">
                  <li><label class="plan-label">贷款期限(月)</label>
                    <RadioGroup v-model="plan1Period" type="button">
                        <Radio :label="12"></Radio>
                    </RadioGroup>
                  </li>
                  <li><label class="plan-label">首付比例</label>{{plan1.firstRate}} %</li>
                  <li><label class="plan-label">首付金额</label>{{plan1.firstPayment}}</li>
                  <li><label class="plan-label">融资比例</label>{{plan1.financeRate}} %</li>
                  <li><label class="plan-label">融资金额</label>{{plan1.financePayment}}</li>
                  <li><label class="plan-label">尾款比例</label>{{plan1.finalRate}} %</li>
                  <li><label class="plan-label">尾款金额</label>{{plan1.finalPayment}}</li>
                </ul>
                <ul class="plan-result">
                  <li><label>月供</label>{{plan1.perMonth}}</li>
                  <li><label>利息(天)</label>{{plan1.interest}}</li>
                </ul>
              </div>
            </Col>

          </Row>
        </Content>
    </Layout>

  </Layout>

  </div>
</template>

<script>
import pmt from 'formula-pmt'
// import plans from 'assets/plans.json'
export default {
  data () {
    return {
      kiloList: [
        {
          value: 300,
          label: '300KM'
        },
        {
          value: 400,
          label: '400KM'
        }
      ],
      planList: [
        {
          value: 'plan1',
          label: '方案一：乐享“无”忧'
        },
        {
          value: 'plan2',
          label: '方案二：轻松入“威”-定额贷'
        },
        {
          value: 'plan3',
          label: '方案三：5050'
        }
      ],
      kilo: 300,
      carPrice: '',
      otherPrice: '',
      percentRate: 8.8,
      plan1Period: 12,
      plan1firstRate: 0.5,
      plan1financeRate: 0.5,
      plan1finalRate: 0.5,

      plan2Period: 24,
      plan2FirstRate: 30,
      plan2FinanceRate: 0.7,
      plan2finalRate: 0,

      plan3Period: 36,
      plan3finalRate: 0.2,

      tab1Show: false,
      tab2Show: false,
      tab3Show: false,
      plan: ''
    }
  },
  computed: {
    total: function () {
      return this.carPrice + this.otherPrice
    },
    rate: function () {
      return this.percentRate / 100
    },
    plan1: function () {
      let _firstPayment = this.plan1firstRate * this.total
      let _financePayment = this.plan1financeRate * this.total
      let _finalPayment = this.plan1finalRate * this.total
      let _perMonth = pmt(this.rate / 12, this.plan1Period, -_financePayment, _finalPayment)
      let _interest = (_perMonth * this.plan1Period - _financePayment + _finalPayment) / 36 / 30
      return {
        firstRate: (this.plan1firstRate * 100).toFixed(2),
        firstPayment: _firstPayment.toFixed(2),
        financeRate: (this.plan1financeRate * 100).toFixed(2),
        financePayment: _financePayment.toFixed(2),
        finalRate: (this.plan1finalRate * 100).toFixed(2),
        finalPayment: _finalPayment.toFixed(2),
        perMonth: _perMonth.toFixed(2),
        interest: _interest.toFixed(2)
      }
    },

    plan2firstRate: function () {
      return this.plan2FirstRate / 100
    },
    plan2financeRate: function () {
      return 1 - this.plan2firstRate
    },
    plan2: function () {
      let _firstPayment = this.plan2firstRate * this.total
      let _financePayment = this.plan2financeRate * this.total
      let _finalPayment = this.plan2finalRate * this.total
      let _perMonth = pmt(this.rate / 12, this.plan2Period, -_financePayment, _finalPayment)
      let _interest = (_perMonth * this.plan2Period - _financePayment + _finalPayment) / 36 / 30
      return {
        firstRate: (this.plan2firstRate * 100).toFixed(2),
        firstPayment: _firstPayment.toFixed(2),
        financeRate: (this.plan2financeRate * 100).toFixed(2),
        financePayment: _financePayment.toFixed(2),
        finalRate: (this.plan2finalRate * 100).toFixed(2),
        finalPayment: _finalPayment.toFixed(2),
        perMonth: _perMonth.toFixed(2),
        interest: _interest.toFixed(2)
      }
    },

    plan3firstRate: function () {
      return this.plan2FirstRate / 100
    },
    plan3financeRate: function () {
      return 1 - this.plan2firstRate
    },
    plan3: function () {
      let _firstPayment
      let _firstRate
      let _financeRate
      let _financePayment
      let _finalPayment
      let _perMonth
      let _interest

      if (this.carPrice === '') {
        _firstRate = 0
        _firstPayment = 0
        _financeRate = 0
        _financePayment = 0
        _finalPayment = 0
        _perMonth = 0
        _interest = 0
      } else {
        if (this.kilo === 300) {
          if (this.plan3Period === 18) {
            _firstPayment = this.total - 50000
          } else {
            _firstPayment = this.total - 68888
          }
        } else {
          if (this.plan3Period === 18) {
            _firstPayment = this.total - 68888
          } else {
            _firstPayment = this.total - 78888
          }
        }
        _firstRate = (this.total - _firstPayment) / this.total
        _financeRate = 1 - _firstRate
        _financePayment = this.total - _firstPayment
        _finalPayment = this.plan3finalRate * this.total
        _perMonth = pmt(this.rate / 12, this.plan3Period, -_financePayment, _finalPayment)
        _interest = (_perMonth * this.plan3Period - _financePayment + _finalPayment) / 36 / 30
      }

      return {
        firstRate: (_firstRate * 100).toFixed(2),
        firstPayment: _firstPayment.toFixed(2),
        financeRate: (_financeRate * 100).toFixed(2),
        financePayment: _financePayment.toFixed(2),
        finalRate: (this.plan3finalRate * 100).toFixed(2),
        finalPayment: _finalPayment.toFixed(2),
        perMonth: _perMonth.toFixed(2),
        interest: _interest.toFixed(2)
      }
    }
  },
  watch: {
    plan: function (event) {
      if (this.plan === 'plan1') {
        this.tab1Show = true
        this.tab2Show = false
        this.tab3Show = false
      } else if (this.plan === 'plan2') {
        this.tab1Show = false
        this.tab2Show = true
        this.tab3Show = false
      } else if (this.plan === 'plan3') {
        this.tab1Show = false
        this.tab2Show = false
        this.tab3Show = true
      } else {}
    }
  }

}
</script>
